import { lazy, useContext } from "react";
import { Routes, Route } from "react-router-dom";
import Layout from "./Layout";
import { AuthContext } from "./AuthContext";

const LandingPage = lazy(() => import("../feautures/LandingPage"));
const Dashboard = lazy(() => import("../feautures/Admin/Dashboard"));
const Branch = lazy(() => import("../feautures/Admin/Reference/Branch"));
const PolicyAccount = lazy(
  () => import("../feautures/Admin/Reference/PolicyAccount")
);
const SubAccount = lazy(
  () => import("../feautures/Admin/Reference/SubAccount")
);
const IDEntry = lazy(
  () => import("../feautures/Admin/Reference/Entry/IDEntry")
);
const Mortgagee = lazy(() => import("../feautures/Admin/Reference/Mortgagee"));
const Booklet = lazy(() => import("../feautures/Admin/Reference/Booklet"));
const Subline = lazy(() => import("../feautures/Admin/Reference/Subline"));
const Rates = lazy(() => import("../feautures/Admin/Reference/Rates"));
const CTPL = lazy(() => import("../feautures/Admin/Reference/CTPL"));
const PettyCashTransaction = lazy(
  () => import("../feautures/Admin/Reference/PettyCashTransaction")
);

const Policy = lazy(
  () => import("../feautures/Admin/Task/Production/Policies/Policy")
);
const TemporaryPolicy = lazy(
  () => import("../feautures/Admin/Task/Production/TemporaryPolicy")
);
const StatementAccount = lazy(
  () => import("../feautures/Admin/Task/Production/StatementAccount")
);
const ListReports = lazy(
  () => import("../feautures/Admin/Reports/ListReports")
);
const About = lazy(() => import("../feautures/Admin/Help/About"));
const Index = lazy(() => import("../feautures/Admin/Help/Index"));
const Search = lazy(() => import("../feautures/Admin/Help/Search"));
const Content = lazy(() => import("../feautures/Admin/Help/Content"));

const VehiclePolicy = lazy(
  () =>
    import(
      "../feautures/Admin/Task/Production/Policies/PolicyComponent/Vehicle/VehiclePolicy"
    )
);
const FirePolicy = lazy(
  () =>
    import(
      "../feautures/Admin/Task/Production/Policies/PolicyComponent/Fire/FirePolicy"
    )
);

const MarinePolicy = lazy(
  () =>
    import(
      "../feautures/Admin/Task/Production/Policies/PolicyComponent/Marine/MarinePolicy"
    )
);
const BondsPolicy = lazy(
  () =>
    import(
      "../feautures/Admin/Task/Production/Policies/PolicyComponent/Bonds/BondsPolicy"
    )
);
const MSPRPolicy = lazy(
  () =>
    import(
      "../feautures/Admin/Task/Production/Policies/PolicyComponent/MSPR/MSPRPolicy"
    )
);
const PAPolicy = lazy(
  () =>
    import(
      "../feautures/Admin/Task/Production/Policies/PolicyComponent/PAPolicy"
    )
);
const CGLPolicy = lazy(
  () =>
    import(
      "../feautures/Admin/Task/Production/Policies/PolicyComponent/CGLPolicy"
    )
);

const PolicyInformation = lazy(
  () =>
    import(
      "../feautures/Admin/Task/Production/Policies/PolicyComponent/Vehicle/VehicleComponent/PolicyInformation"
    )
);
const PolicyTypeDetails = lazy(
  () =>
    import(
      "../feautures/Admin/Task/Production/Policies/PolicyComponent/Vehicle/VehicleComponent/PolicyTypeDetails"
    )
);
const PolicyPremium = lazy(
  () =>
    import(
      "../feautures/Admin/Task/Production/Policies/PolicyComponent/Vehicle/VehicleComponent/PolicyPremium"
    )
);

const FirePolicyInformation = lazy(
  () =>
    import(
      "../feautures/Admin/Task/Production/Policies/PolicyComponent/Fire/FirePolicyComponents/FirePolicyInformation"
    )
);
const FirePolicyPremium = lazy(
  () =>
    import(
      "../feautures/Admin/Task/Production/Policies/PolicyComponent/Fire/FirePolicyComponents/FirePolicyPremium"
    )
);

const MarinePolicyInformation = lazy(
  () =>
    import(
      "../feautures/Admin/Task/Production/Policies/PolicyComponent/Fire/FirePolicyComponents/FirePolicyInformation"
    )
);
const MarinePolicyPremium = lazy(
  () =>
    import(
      "../feautures/Admin/Task/Production/Policies/PolicyComponent/Marine/MarinePolicyComponents/MarinePolicyPremium"
    )
);

const BondsPolicyPremium = lazy(
  () =>
    import(
      "../feautures/Admin/Task/Production/Policies/PolicyComponent/Bonds/BondsPolicyComponents/BondsPolicyPremium"
    )
);
const BondsPolicyInformation = lazy(
  () =>
    import(
      "../feautures/Admin/Task/Production/Policies/PolicyComponent/Fire/FirePolicyComponents/FirePolicyInformation"
    )
);

const MSPRPolicyInformation = lazy(
  () =>
    import(
      "../feautures/Admin/Task/Production/Policies/PolicyComponent/MSPR/MSPRPolicyComponents/MSPRPolicyInformation"
    )
);
const MSPRPolicyPremium = lazy(
  () =>
    import(
      "../feautures/Admin/Task/Production/Policies/PolicyComponent/MSPR/MSPRPolicyComponents/MSPRPolicyPremium"
    )
);


export default function Routers() {
  const { user } = useContext(AuthContext);

  return (
    <Routes>
      {user ? (
        <Route path="/dashboard" element={<Layout />}>
          <Route path="/dashboard" element={<Dashboard />} />
          <Route path="/dashboard/reference/branch" element={<Branch />} />
          {/* Reference */}
          <Route
            path="/dashboard/reference/policy-account"
            element={<PolicyAccount />}
          />
          <Route
            path="/dashboard/reference/sub-account"
            element={<SubAccount />}
          />
          <Route path="/dashboard/reference/id-entry" element={<IDEntry />} />
          <Route
            path="/dashboard/reference/mortgagee"
            element={<Mortgagee />}
          />
          <Route path="/dashboard/reference/booklet" element={<Booklet />} />
          <Route path="/dashboard/reference/subline" element={<Subline />} />
          <Route path="/dashboard/reference/rates" element={<Rates />} />
          <Route path="/dashboard/reference/ctpl" element={<CTPL />} />
          <Route
            path="/dashboard/reference/petty-cash-transaction"
            element={<PettyCashTransaction />}
          />
          {/* Production */}
          <Route path="/dashboard/task/production/policy" element={<Policy />}>
            <Route
              path="/dashboard/task/production/policy"
              element={<VehiclePolicy />}
            >
              <Route index element={<PolicyInformation />} />
              <Route
                path="vehicle/policy-type-details"
                element={<PolicyTypeDetails />}
              />
              <Route
                path="vehicle/policy-premium"
                element={<PolicyPremium />}
              />
            </Route>

            <Route path="fire" element={<FirePolicy />}>
              <Route index element={<FirePolicyInformation />} />
              <Route path="policy-premium" element={<FirePolicyPremium />} />
            </Route>
            <Route path="marine" element={<MarinePolicy />}>
              <Route index element={<MarinePolicyInformation />} />
              <Route path="policy-premium" element={<MarinePolicyPremium />} />
            </Route>
            <Route path="bonds" element={<BondsPolicy />}>
              <Route index element={<BondsPolicyInformation />} />
              <Route path="policy-premium" element={<BondsPolicyPremium />} />
            </Route>
            <Route path="mspr" element={<MSPRPolicy />}>
              <Route index element={<MSPRPolicyInformation />} />
              <Route path="policy-premium" element={<MSPRPolicyPremium />} />
            </Route>
            <Route path="pa" element={<PAPolicy />} />
            <Route path="cgl" element={<CGLPolicy />} />
          </Route>
          <Route
            path="/dashboard/task/production/temporary-policy"
            element={<TemporaryPolicy />}
          />
          <Route
            path="/dashboard/task/production/statement-of-account"
            element={<StatementAccount />}
          />
          {/* Reports */}
          <Route
            path="/dashboard/reports/list-of-reports"
            element={<ListReports />}
          />
          {/* Help */}
          <Route path="/dashboard/help/content" element={<Content />} />
          <Route path="/dashboard/help/search" element={<Search />} />
          <Route path="/dashboard/help/index" element={<Index />} />
          <Route path="/dashboard/help/about" element={<About />} />
        </Route>
      ) : (
        <Route path="/">
          <Route index element={<LandingPage />} />
        </Route>
      )}
      <Route path="*" element={<div>No Matches url</div>} />
    </Routes>
  );
}
