export function RenderSidebarItems({ item }: { item: any }) {
  const [showDropdown, setShowDropDown] = useState(false);
  const { searchParams } = useUrlParams();
  const location = useLocation();
  const handleClick = () => {
    setShowDropDown((d) => !d);
  };

  return (
    <>
      <ListItemButton sx={{ pl: 2 }} onClick={handleClick}>
        <ListItemIcon>{item.icon}</ListItemIcon>
        <ListItemText primary={item.text} />
        {showDropdown ? <ExpandLess /> : <ExpandMore />}
      </ListItemButton>
      <Collapse in={showDropdown} timeout="auto" unmountOnExit>
        <List component="div" disablePadding>
          {item.children &&
            item.children.map((child1: any, index: number) => {
              return (
                <React.Fragment key={index}>
                  {child1.children ? (
                    <Box>
                      <Divider />
                      <RenderSidebarItems item={child1} />
                    </Box>
                  ) : (
                    <React.Fragment>
                      <Divider />
                      <ListItemButton
                        selected={
                          child1.text === "Policy"
                            ? policyLinkList.some((d) =>
                                d.includes(location.pathname)
                              )
                            : location.pathname === child1.link
                        }
                        component={Link}
                        sx={{ pl: 4 }}
                        key={index}
                        to={
                          child1.link +
                          `?drawer=${searchParams.get(
                            "drawer"
                          )}&pageSize=${searchParams.get("pageSize")}`
                        }
                      >
                        <ListItemIcon>{child1.icon}</ListItemIcon>
                        <ListItemText primary={child1.text} />
                      </ListItemButton>
                    </React.Fragment>
                  )}
                </React.Fragment>
              );
            })}
        </List>
      </Collapse>
      <Divider />
    </>
  );
}
