import { useContext } from "react";
import Drawer from "@mui/material/Drawer";
import List from "@mui/material/List";
import Divider from "@mui/material/Divider";
import { ListItemButton, ListItemIcon, ListItemText } from "@mui/material";
import LogoutIcon from "@mui/icons-material/Logout";
import {
  RenderSidebarItems,
  SidebarHeader,
  sidebarOptions,
} from "./SidebarOptions";
import { AuthContext } from "../AuthContext";
import { useNavigate } from "react-router-dom";
import { useQuery } from "react-query";
import { Logout } from "./Logout";
import { wait } from "../../lib/wait";
import Swal from "sweetalert2";

export default function SidebarDesktop({
  drawerWidth,
  handleDrawerClose,
  open,
  setOpenCircularLoader,
}: {
  open: boolean;
  drawerWidth: number;
  handleDrawerClose: CallableFunction;
  setOpenCircularLoader: React.Dispatch<React.SetStateAction<boolean>>;
}) {
  const { setUser, myAxios, user } = useContext(AuthContext);
  const navigate = useNavigate();

  const { refetch } = useQuery({
    queryKey: "logout",
    queryFn: async () =>
      wait(1200).then(async () => await Logout(myAxios, user)),
    enabled: false,
    onSuccess: (res) => {
      setOpenCircularLoader(false);
      if (res.data.success) {
        Swal.fire({
          position: "center",
          icon: "success",
          title: res.data.message,
          showConfirmButton: false,
          timer: 800,
        }).then(() => {
          setUser(null);
          navigate("/");
        });
      }
    },
  });

  const handleLogout = () => {
    setOpenCircularLoader(true);
    refetch();
  };

  return (
    <Drawer
      sx={{
        width: drawerWidth,
        flexShrink: 0,
        "& .MuiDrawer-paper": {
          width: drawerWidth,
          boxSizing: "border-box",
        },
      }}
      variant="persistent"
      anchor="left"
      open={open}
    >
      <SidebarHeader handleDrawerClose={handleDrawerClose} />
      <Divider/>
      <List sx={{marginTop:0,paddingTop:0}}>
        {sidebarOptions.map((item, index) => {
          return <RenderSidebarItems key={index} item={item} />;
        })}
        <ListItemButton sx={{pl:2}} onClick={handleLogout}>
          <ListItemIcon>
            <LogoutIcon />
          </ListItemIcon>
          <ListItemText primary={"Logout"} />
        </ListItemButton>
      </List>
    </Drawer>
  );
}
